h1 {
    font-size: 60px;
    font-weight: 900;
}

h2 {
    font-size: 32px;
}

h3 {
    font-size: 24px;
    font-weight: bold;
}

h4 {
    font-size: 18px;
    font-weight: normal;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Inter', sans-serif;
    color: #2D3436;
}

.container {
    padding-top: 64px;
}
.page_1_container {
    padding-top: 80px;
    width: 100%;
}

.page_1_content {
    width: 1300px;
    position: relative;
    left: calc(50vw - (1150px) / 2);
}

.page_2_container {
    min-height: calc(105vh - 64px);
    background-color: #F8F8F8;
    padding-top: 25px;
    padding-bottom: 100px;
}


#student_img_container {
    float: right;
    width: 50%;
    shape-outside: circle(50%);
    margin-left: 50px;
}

#student_img_container > img {
    width: 100%;
    height: auto;
    pointer-events: none;
}

.first_header, .second_header {
    font-weight: 500;
}


.first_header {
    /*width: 65vw; */
    line-height: 40px;
}
.second_header {
    margin-top: 80px;
    margin-bottom: 50px;
    line-height: 32px;
    /*width: 40vw; */
}


.button_container {
    display: flex;
    justify-content: center;
}

#ground {
    background-color: #F8F8F8;
    height: 155.7px;
    width: 100%;
    position: absolute;
    z-index: -1;
    top: 624.3px;
}

.page_2_header_container {
    width: 45vw;
    text-align: center;
    margin-left: 27.5vw;
}

.page_2_header {
    font-weight: 600;
}

.info_header {
    color: #DB0202;
    font-size: 28px;
    margin-bottom: 5px;
    white-space: nowrap;
    font-family: 'Inter', sans-serif;
    font-weight: 700;
}

.info:first-of-type {
    margin-right: 10vw;
}


.list_text {
    padding-top: 0px;
    margin: 0px;
    line-height: 32px;
}